<template>
  <div class="modal" @click.stop="go">
    <div class="txt">
      <p>{{text || firstText}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyDialog",
  components: {},
  props: {
    texts: Array
  },
  data: function() {
    return {
      text: null,
      nextIndex:1,
      turnblack:false,
    };
  },
  computed: {
    it: function() {
      return this.makeIterator(this.texts);
    },
    firstText: function() {
      return this.texts[0];
    }
  },
  methods: {
    makeIterator(array) {
      let that=this;
      return {
        next: function() {
          return that.nextIndex < array.length
            ? { value: array[that.nextIndex++], done: false }
            : { done: true };
        }
      };
    },
    go() {
      let item = this.it.next();
      if (item.done) {
        this.text = null;
        this.$parent.hideDlg=true;
        if(this.turnblack){
          this.$parent.turnblack();//
          this.turnblack=false;
        }
        if(this.firstText===""){
          this.$parent.hideblack();
        }
        if(this.firstText===""){
          this.$parent.$parent.changescene("kitchen");
          this.$parent.fealock=true;
          this.$parent.hideDlg=false;
          this.$parent.dlgTexts=[""];
        }
        if(this.firstText===""){
          this.$parent.showentry1=true;
        }
        if(this.firstText===""){
          this.$parent.$parent.havebag=false;
          this.$parent.showend2=true;
          setTimeout(() => {
            this.$parent.$parent.level2done();
          }, 6000);
         
        }
        if(this.firstText===""){
         this.$parent.$parent.changescene("livingroom");
         
         this.$parent.hideDlg=false;
         this.$parent.showmom=true;
         this.$parent.dlgTexts=["",
         "",
         "",
         "","",
         "","",
         "",
         ""];
        }
        if(this.firstText===""){
          this.$parent.$parent.havebag=false;
          this.$parent.showend1=true;
          setTimeout(() => {
            this.$parent.$parent.level1done();
          }, 6000);
         
        }
        if(this.firstText===""){
         this.$parent.$parent.changescene("livingroom");
         this.$parent.hideDlg=false;
         this.$parent.showmom=false;
         this.$parent.showson=true;
         this.$parent.dlgTexts=["",
         "",
         "",
         "",
         "","",
         "",
         "()","()"];
        }
        if(this.firstText===""){
         this.$parent.level3done();
        }
        
      } else {
        
        if(item.value===""){
          this.$parent.showson=false;
        }else if(item.value===""){
          this.$parent.showson=true;
          this.turnblack=true;
        }else if(item.value===""){
          this.$parent.showson=false;
          this.$parent.showmom=true;
        }else if(item.value===""||
        item.value===""||
        item.value==="QQ"||
        item.value===""){
          this.$parent.showmom=true;this.$parent.showson=false;
        }else if(item.value===""||
        item.value===" "){
          this.$parent.showmom=false;this.$parent.showson=true;
          }else if(item.value===""||item.value===""){
            this.$parent.showmom=true;
          }else if(item.value===""
          ||item.value===""){
            this.$parent.showmom=false;
          }else if(item.value===""||item.value===""){
            this.$parent.showmom=true;
            this.$parent.showson=false;
          }else if(item.value===""){
            this.$parent.showmom=false;
            this.$parent.showson=true;
          }
          this.text = item.value;
      }
    }
  },
  created() {}
};
</script>

<style scoped>
.modal{
  position: absolute;
  width: 100%;
  height: 270px;
  bottom: 0;
  background-image: url("/static/1_0/dialogbg.png");
}

p {
  font-size: 50px;
  font-family: "myfont";
  color: white;
  line-height: 70px;
  /*-webkit-text-stroke: 0.5px #ffffff;*/
  /*text-shadow: 0px 0px 0px #ffffff;*/
}
.txt {
  margin-left: 390px;
  width: 1400px;
  margin-top: 65px;
  cursor: pointer;
}
</style>